.menu-btn {
  display: none;
  @include respond(tablet) {
    display: inline-block;
  }
}

.nav {
  @include respond(tablet) {
    display: none;
  }
  &__list {
    li {
      display: inline-block;
      line-height: 35px;
      &:not(:last-child) {
        margin-right: $margin-lg;
      }
    }
  }
}
